import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import articles from './article-content';
import { NotFoundPage } from './NotFoundPage';
import { CommentsList } from '../components/CommentsList';

export const ArticlePage = () => {

    const { name } = useParams();

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    const [nameValue, setNameValue] = useState('');
    const [commentValue, setCommentValue] = useState('');

    useEffect(() => {
        //setArticleInfo({upvotes: 3});
        const fetchARticleInfo = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        }

        fetchARticleInfo();
    }, [name]);

    const article = articles.find(a => a.name === name)

    const upvoteARticle = async () => {
        const response = await fetch(`/api/articles/${article.name}/upvote`, {
            method: 'post',
        });

        const body = await response.json();
        setArticleInfo(body);
    }

    const addComment = async () => {
        const response = await fetch(`/api/articles/${article.name}/comment`, {
            method: 'post',
            body: JSON.stringify({comment: {postedBy: nameValue, text: commentValue}}),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const body = await response.json();
        setArticleInfo(body);
        setNameValue('');
        setCommentValue('');
    }

    return article ? (
        <>
            <h1>{article.title}</h1>
            <div id="upvotes-section">
                <p>This article has {articleInfo.upvotes} upvotes</p>
                <button onClick={upvoteARticle}>Add Upvote</button>
            </div>
            {article.content.map(paragraph => (
                <p key={paragraph}>{paragraph}</p>
            ))}

            <div id="add-comment-form">
                <h3>Add a Comment</h3>
                <label>
                    Name:
                    <input
                        type = "text" 
                        value={nameValue}
                        onChange={ e => setNameValue(e.target.value)}/>

                </label>
                <label>
                    Comment:
                    <textarea rows="4" cols="50" value={commentValue}
                     onChange={ e => setCommentValue(e.target.value)} />
                </label>
                <button onClick={addComment}>Add Comment</button>
            </div>

            <CommentsList comments={articleInfo.comments} />
        </>
    ) : (

            <NotFoundPage />
        );
}
